body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  font-family: "NanumSquareOTFB";
  box-sizing: border-box;
  -webkit-overflow-scrolling: touch;
  
}
body {
  height: 100%;
  width: 100%;
  background-image: -webkit-gradient(linear, left top, right bottom, from(#989ade), to(#5f6ccc));
  background-image: -webkit-linear-gradient(top left, #989ade, #5f6ccc);
  background-image: linear-gradient(to bottom right, #989ade, #5f6ccc);

}
.MuiFormControl-root-59 {
  width: 100%;
}
html,
body > #root {
  height: 100%;
  width: 100%;
}
